<template>
	<div class="" >
		<div class="playlist" v-show="showFlag" @click="hide">
			<div class="list-wrapper" @click.stop>
				<div class="list-header">
					<h1 class="title">
						<i class="icon"></i>
						<span class="text"></span>
						<span class="clear" @click="sureclear">
							<i class="icon-clear"></i>
						</span>
					</h1>
				</div>
				<scroll class="list-content" :data="newmimiList" ref="listcontent">
					<transition-group name="list" tag="ul">
						<li :key="item.id" ref="listItem" @click="selsecItme(item,index)" class="item" v-for="(item ,index) in newmimiList">
							<i class="current" :class="getCurrentIcon(item)"></i>
							<span class="text">{{item.name}}</span>
							<span class="like" @click.stop="toggleFavorite(item)">
								  <i  class="icon" :class="getFavoriteIcon(item)"></i>
							</span>
							<span class="delete" @click.stop="delectOne(item)">
								<i class="icon-delete"></i>
							</span>
						</li>
					</transition-group>
				</scroll>
				<div class="list-operate">
					<div class="add" @click="addSong">
						<i class="icon-add" ></i>
						<span class="text">添加歌曲到队列</span>
					</div>
				</div>
				<div class="list-close" @click="hide">
					<span>关闭</span>
				</div>
			</div>
		<confirm ref="confirm"  text="是否清空播放列表" confirmBtnText="清空" @confirm="confirmClear">			
		</confirm>
		  <add-song ref="addSong"></add-song>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
  import {mapActions,mapGetters,mapMutations} from 'vuex'
  import {playMode} from 'common/js/config'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import AddSong from 'components/add-song/add-song'
  import {playerMixin} from 'common/js/mixin'
	
	export default{
		data(){
			return {
				showFlag:false,
				newindex:''
			}
		},
		computed:{
			...mapGetters({
				newmimiList:'sequenceList',
				currentSongs:'currentSong',
				playlist:'playlist',
				mode:'mode',
				favoriteList:'favoriteList'
			})
		},
		methods:{
			show(){
				this.showFlag = true 
				setTimeout(() =>{
					this.$refs.listcontent.refresh()
					this.scrollToCurrent(this.currentSongs)
				},20)
			},
			hide(){
				this.showFlag = false
			},
			getCurrentIcon(item){
				if(this.currentSongs.id === item.id){
					return `icon-play`
				}
				return ''
			},
			selsecItme(item,index){
				/*index = this.playlist.findIndex((song) => {
					return song.id === item.id
				})*/
				this.setCurrentIndex(index)
				this.setPlayingState(true)
			},
			//选中的图标滚动到第一个位置
			scrollToCurrent(current){
				const index =this.newmimiList.findIndex((song) => {
					return current.id === song.id
				})
				this.$refs.listcontent.scrollToElement(this.$refs.listItem[index],300)
			},
			delectOne(item){
				this.deleteSong(item)
				this.setPlayingState(true)
				if(!this.playlist.length){
					this.hide()
				}
			},
			sureclear(){
				this.$refs.confirm.show()
			},
			confirmClear(){
				this.deleteSongList()
                this.hide()
				this.setPlayingState(false)
			},
			addSong(){
				this.$refs.addSong.show()
			},
			...mapMutations({
				setCurrentIndex:'SET_CURRENT_INDEX',
				setPlayingState:'SET_PLAYING_STATE'
			}),
			//收藏喜欢的
			getFavoriteIcon(song){
			if(this.isFavorite(song)){
				return 'icon-favorite'
			}
			return 'icon-not-favorite'
		},
		toggleFavorite(song){
			if(this.isFavorite(song)){
				this.deleteFavoriteList(song)
			}else{
				this.saveFavoriteList(song)
			}			
		},
		   isFavorite(song){
			//console.log(JSON.stringify(this.favoriteList))
			const index = this.favoriteList.findIndex((item) => {
				return item.id === song.id
			})
			return index >-1
		 },
			...mapActions([
				'deleteSong',
				'deleteSongList',
				'saveFavoriteList',
			    'deleteFavoriteList'
			])
		},
		watch:{
			currentSong(newSong){
				if(!this.showFlag || newSong.id === oldSong.id){
					return
				}
				this.scrollToCurrent(newSong)
			}
		},
		components:{
			Scroll,
			Confirm,
			AddSong
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



